import {useDispatch, useSelector} from "react-redux";
import {logout, selectLogin} from "../Auth/AuthSlice";
import './Main.css'
import {InteractiveElement} from "../InteractiveElement/InteractiveElement";
import {FineSettingsFunc} from "../FineSettings/FineSettingsFunc";
import {PointTable} from "../Table/PointTable";

export const Main = (props) => {
    const dispatch = useDispatch();
    const login = useSelector(selectLogin);

    const tryLogout = () => {
        dispatch(logout());
        localStorage.clear();
        props.history.push('/');
    };

    return (
        <div>
            <div className="header">
                <p>You are {login}</p>
                <button className="btn btn-warning" onClick={tryLogout}>Logout</button>
            </div>

            <div className="interface row">
                <InteractiveElement/>
                <FineSettingsFunc/>
                <PointTable/>
            </div>
        </div>
    );
}